import React from "react";
import falv from '../pages/falv.png';
//import {withRouter} from "react-router-dom";
//import {connect} from 'react-redux';
//import {datalist} from "../reduxDemos/actionCreators";
class Home extends React.Component{
    // constructor(props){
    //     super(props);

    // }
    // componentDidMount(){

    // }
    render(){
        return (
            <div>    
                {/* 导航部分 */}
                <div style={{width:'100%',height:'90px',backgroundColor:'#056cae' ,color:'white',marginTop:'-15px'}}>
                    <img src={falv} alt='falv' style={{width:'80px',height:'70px',display:'inline-block',marginTop:'20px',marginLeft:'20px'}}/> 
                    <span style={{position:'relative',bottom:'30px',fontSize:'20px'}}>法援宝后台管理系统</span>
                    <button onClick={()=>{this.props.history.push('/login')}} style={{float:'right',marginTop:'40px',marginRight:'20px'}}>登录</button> 
                </div>    
                {/*按钮部分*/}
                <div style={{backgroundColor:"#2c3b58",width:'200px',height:'500px',marginTop:'-18px'}}>
                    <ul style={{listStyle:'none'}}>
                        <li onClick={()=>{this.props.history.push('/user')}} style={{width:'200px',height:'70px',marginTop:'18px'}}> <i className='iconfont icon-yonghu'></i><button style={{backgroundColor:'#2c3b58',color:'white',borderColor:'#2c3b58',borderWidth:'0px'}}>用户信息</button></li>
                        <li onClick={()=>{this.props.history.push('/lawyer')}} style={{width:'200px',height:'70px'}}> <i className='iconfont icon-lvshi'></i><button style={{backgroundColor:'#2c3b58',color:'white',borderColor:'#2c3b58',borderWidth:'0px'}}>律师信息</button></li>
                        <li onClick={()=>{this.props.history.push('/info')}} style={{width:'200px',height:'70px'}}> <i className='iconfont icon-shezhi'></i><button style={{backgroundColor:'#2c3b58',color:'white',borderColor:'#2c3b58',borderWidth:'0px'}}>设 &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;置</button></li>
                        <li onClick={()=>{this.props.history.push('/setup')}} style={{width:'200px',height:'70px'}}> <i className='iconfont icon-xiaoxi'></i><button style={{backgroundColor:'#2c3b58',color:'white',borderColor:'#2c3b58',borderWidth:'0px'}}>消息中心</button></li>
                        <li onClick={()=>{this.props.history.push('/consult')}} style={{width:'200px',height:'70px'}}> <i className='iconfont icon-zixunxinxi'></i><button style={{backgroundColor:'#2c3b58',color:'white',borderColor:'#2c3b58',borderWidth:'0px'}}>咨询信息</button></li> 
                    </ul>
                </div>
            </div>
        )
    }

}
export default Home;